.main {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.card {
  display: flex;
  flex-direction: column;
  height: 100%;
  border: 1px solid plum;
  border-radius: 25px;
  width: 50%;
  margin: 120px;
  margin-bottom: -50px;
  padding: 20px;
  background-color: white;
}
.description {
  display: flex;
  align-items: center;
}
.downCard {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  padding-top: 10px;
}
.link {
  color: rgb(54, 54, 102);
  text-decoration: none;
}
img {
  width: 100px;
  height: 200px;
  margin-left: 100px;
}
h5 {
  font-size: 17px;
}
@media (max-width: 480px) {
  .card {
    display: flex;
    flex-direction: column;
    height: 100%;
    border: 1px solid plum;
    border-radius: 25px;
    width: 80%;
    padding: 20px;
    background-color: white;
    text-decoration: none;
    color: rgb(54, 54, 102);
  }
  img {
    width: 99px;
    height: 200px;
  }
  h5 {
    font-size: 12px;
  }
}
